import { Notification } from 'element-ui';
import { list } from '@/api/faas';
export default {
  components: {},
  created() {
    this.initData();
  },
  data() {
    return {
      search_input: '',
      tableData: []
    };
  },
  watch: {},
  computed: {},
  methods: {
    initData() {
      list().then(res => {
        if (res.message == '') {
          this.tableData = res.data;
        } else {
          Notification.error({
            title: 'some error happened: ' + res.message,
            duration: 5000
          });
        }
      }).catch(err => {
        Notification.error({
          title: err,
          duration: 5000
        });
      });
    }
  }
};